export abstract class AbstractEnv implements AbstractEnvGetterJSON {
    abstract getReqPrefix(): string;
    abstract getSessionID(): string;
    abstract getVersion(): string;
    abstract getFineServletURL(): string;
    abstract getInitParameters(): {[key: string]: any} | undefined;
    abstract getServletURL(): string;

    public toJSON(): AbstractEnvValueJSON {
        return {
            reqPrefix: this.getReqPrefix(),
            sessionID: this.getSessionID(),
            version: this.getVersion(),
            fineServletURL: this.getFineServletURL(),
            initParameters: this.getInitParameters(),
            servletURL: this.getServletURL(),
        };
    }
}

export type AbstractEnvJSON = AbstractEnvValueJSON | AbstractEnvGetterJSON;

export interface AbstractEnvValueJSON {
    reqPrefix: string;
    sessionID: string;
    version: string;
    fineServletURL: string;
    initParameters?: {
        [key: string]: any;
    };
    servletURL: string;
}

interface AbstractEnvGetterJSON {
    getReqPrefix: (() => string);
    getSessionID: (() => string);
    getVersion: (() => string);
    getFineServletURL: (() => string);
    getInitParameters: (() => {[key: string]: any} | undefined);
    getServletURL: (() => string);
}
